.container {
  max-width: 1020px;
  width: 90%;
  margin: 30px auto;
  border-radius: 12px;
  background: #c8e2e3;
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #15171c;
}

.pdf {
  width: 100%;
  height: 500px;
  margin: 40px 0;
  border-radius: 12px;
}

.container h1 {
  font-size: 40px;
  margin: 50px 0;
}

.pdfBtn {
  background-color: #37696c;
  padding: 1.1em 3.8em;
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
  cursor: pointer;
  border: none;
  border-radius: 2em;
  color: #e1e9fc;
  font-size: 1em;
  text-align: center;
  transition: background 0.4s ease;
}

.container p {
  padding: 30px;
  margin-bottom: 1.5rem;
  margin-top: 0.6rem;
  font-size: 1.1em;
  line-height: 1.5em;
}

.pdfBtn:hover {
  background-color: #2d5557;
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.form label {
  margin-left: 0.6em;
  font-size: 1.1em;
}

.checkbox {
  border-radius: 0.25em;
  width: 1rem;
  height: 1rem;
}
